.map{
    height: 400px;
    background-color: white;
    padding: 1rem;
    border: 2px solid black;
    border-radius: 20px;
    margin-top: 20px;
    box-shadow: 0 0 8px -4px rgba(0, 0, 0, 0.5);
}

@media(max-width: 950px) {
    .map{
        margin: 20px;
    }
}

.map .leaflet-container{
    height: 100%;
}

.info-container{
    box-sizing: border-box;
}

.info-flag{
    height: 100px;
    background-position: center;
    background-size: cover;
    border-radius: 10px;
    border: 1px solid black;
    margin-bottom: 10px;
}

.info-name{
    font-size: 20px;
    font-weight: bold;
    color: rgb(99, 99, 99);
}

.info-confirmed,
.info-recovered,
.info-deaths{
    font-size: 16px;
    margin-top: 5px;
}