*{
  margin: 0;
}

body{
  background-color: #f5f6fa;
}

.app{
  margin-top: 20px;
  display: flex;
  justify-content: space-evenly;
}

.app__header{
  color: rgb(139, 139, 139);
  display: flex;
  align-items: center;
  margin-bottom: 20px;
  justify-content: space-between;
}

.app__dropdown{
  background-color: white;
}

.app__stats{
  display: flex;
  justify-content: space-between;
}

.app__left{
  flex: 0.9;
}

.app__graph{
  margin-top: 20px;
  margin-bottom: 30px;
  height: 200px;
  width: 400px
}

.app__errorMessage{
  text-align: center;
  padding: 20px 0;
}

@media(max-width: 950px){
  .app{
    flex-direction: column;
  }

  .app__header{
    justify-content: space-evenly;
  }

  .app__stats{
    justify-content: space-evenly;
  }

  .app__right{
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .app__right h3{
    text-align: center;
  }
}

@media(max-width: 480px) {
  .app__header{
    flex-direction: column;
  }

  .app__header h1{
    margin-bottom: 10px;
  }

  .app__stats{
    flex-direction: column;
    width: 200px;
    justify-content: space-evenly;
    margin: auto;
  }

  .app__graph{
    width: 90%;
  }
}

  @media(max-width: 300px) {
    .app__header{
      text-align: center;
    }

    .app__header h1{
      margin-bottom: 10px;
    }
}