.infoBox{
    margin: 10px;
    flex: 1;
    cursor: pointer;
  }

.infoBox--red{
    border-top: 10px solid red;
}

.infoBox--green{
    border-top: 10px solid greenyellow;
}

.infoBox--gray{
    border-top: 10px solid gray
}

.infoBox__total__red{
    color: red !important;
}

.infoBox__total__green{
    color: greenyellow;
}

.infoBox__total__gray{
    color: gray;
}